.main-container{
    padding-top: 80px;
    padding-bottom: 80px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;   
    align-items: center; 
    text-align: center;
}
.button{
    padding-top: 15px;
}
.entry-with{
    color: #ED1D4A;
}
.inputs{
    padding-top: 30px;
}
.input{
    text-align: center;
    height: 30px;
    width: 320px;
    border-radius: 6px;
    border: 0.5px solid #BCC2C7;
}
.button-forgot{
    background-color:transparent;
    border:none;
    color:gray;
    text-decoration: underline;
}
.button-regisrtation{
    background-color:transparent;
    border:none;
    color:red;
}
.button-entry{
   
    height: 40px;
    color: white;
    background-color: #E7C9D2;
    border-radius: 6px;
    border: none;
}
.big-logo{
    position: fixed;
    right: 1000px;
    bottom: 10px;
}

.responsive {
    width: 100%;
    height: auto;
    
}